* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    font-family: $font-primary;
    font-size: $font-size;
    background-color: $bg-color;
    color: $text;
}

body {
    display: flex;
    flex-direction: column;
    min-width: 100vh;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: 600;
    margin-bottom: 0,5em;
    color: $title-color;
}

h1 {
    font-size: 3rem;
}

h2 {
    font-size: 2,37rem;
}

h3 {
    font-size: 2rem;
}

h4 {
    font-size: 1,75rem;
}

h5 {
    font-size: 1,5rem;
}

h6 {
    font-size: 1.25rem;
}

img {
    height: auto;
    max-width: 100%;
}

.container {
    max-width: 1274px;
    padding: 0 12px;
    margin-left: auto;
    margin-right: auto;
}

::selection {
    color: $bg-color;
    background-color: $primary-color;
}
